import Enum from './enumType';
let enums = {};

enums.AccountPostingType = () => {
    return new Enum([
        {key: 'balanceSheet', display: '  '},
        {key: 'benefitAndLoss', display: '  '}
    ]);
};

enums.AccountBalanceType = () => {
    return new Enum([
        {key: 'debit', display: ''},
        {key: 'credit', display: ''}
    ]);
}

enums.AssignmentStatus = () => {
    return new Enum([
        {key: 'Required', display: ' '},
        {key: 'NotRequired', display: ' '},
        {key: 'DoesNotHave', display: ''}
    ]);
}

enums.JournalType = () => {
    return new Enum([
        {key: 'Opening', display: ''},
        {key: 'Closing', display: ''}
    ]);
}

enums.JournalStatus = () => {
    return new Enum([
        {key: 'Temporary', display: ''},
        {key: 'BookKeeped', display: ' '},
        {key: 'Fixed', display: ' '}
    ]);
};

enums.Active = () => {
    return new Enum([
        {key: true, name: 'showActiveItems', display: '  '},
        {key: false, name: 'showInactiveItems', display: '   '}
    ]);
};

enums.ChequeCategoryStatus = ()=> {
    return new Enum([
        {key: 'Open', display: ''},
        {key: 'Closed', display: ''}
    ]);
};

export default enums;